<template>
  <v-app id="inspire">
    <v-navigation-drawer v-model="drawer" app>
      <v-list dense>
        <div v-if="!loggedAs">
          <v-list-item link to="/login">
            <v-list-item-action>
              <v-icon>mdi-login</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>Login</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </div>
        <div v-else>
          <v-list-item link to="/">
            <v-list-item-action>
              <v-icon>mdi-home</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>Home</v-list-item-title>
            </v-list-item-content>
          </v-list-item>

          <v-list-item link to="/students">
            <v-list-item-action>
              <v-icon>mdi-school</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>Students</v-list-item-title>
            </v-list-item-content>
          </v-list-item>

          <v-list-item link to="/teachers" v-if="loggedAs == 'school'">
            <v-list-item-action>
              <v-icon>mdi-account</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>Teachers</v-list-item-title>
            </v-list-item-content>
          </v-list-item>

          <v-list-item link to="/courses" v-if="loggedAs == 'school'">
            <v-list-item-action>
              <v-icon>mdi-book</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>Courses</v-list-item-title>
            </v-list-item-content>
          </v-list-item>

          <v-list-item link to="/offers">
            <v-list-item-action>
              <v-icon>mdi-teach</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>Offers</v-list-item-title>
            </v-list-item-content>
          </v-list-item>

          <v-list-item link to="/test" v-if="loggedAs == 'school'">
            <v-list-item-action>
              <v-icon>mdi-test</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>Test</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </div>
      </v-list>

      <template v-slot:append>
        <div class="pa-2">
          <v-switch
            v-model="darkMode"
            prependIcon="mdi-theme-light-dark"
            color="white"
            label="Dark Mode"
          >
          </v-switch>
        </div>
        <div class="pa-2" v-if="loggedAs">
          <v-btn @click="logout" block color="red" tile dark>Logout</v-btn>
        </div>
      </template>
    </v-navigation-drawer>

    <v-app-bar app>
      <v-app-bar-nav-icon @click.stop="drawer = !drawer" />
    </v-app-bar>

    <v-content>
      <v-container class="fill-height" fluid align-start>
        <v-row align="start" justify="start">
          <v-col cols="12">
            <router-view></router-view>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
export default {
  created() {
    this.$vuetify.theme.dark = this.$store.state.darkMode;
  },
  props: {
    source: String
  },
  data: () => ({
    drawer: null
  }),
  methods: {
    logout() {
      localStorage.removeItem("token");
      this.$store.commit("setLoggedAs", "");
      this.$store.commit("setToken", "");
      this.$router.push("Login");
    }
  },

  computed: {
    loggedAs() {
      return this.$store.state.loggedAs;
    },
    darkMode: {
      get() {
        return this.$store.state.darkMode;
      },
      set(val) {
        this.$store.commit("setDarkMode", val);
        this.$vuetify.theme.dark = val;
        return val;
      }
    }
  }
};
</script>

<style>
</style>