import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import store from '../store/index'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('../views/Login.vue')
  },
  {
    path: '/test',
    name: 'Test',
    component: () => import('../views/Test.vue')
  },
  {
    path: '/students',
    name: 'Students',
    component: () => import('../views/Students.vue')
  },
  {
    path: '/student/:studentId',
    name: 'ViewStudent',
    props: { student: null },
    component: () => import('../views/ViewStudent.vue')
  },
  {
    path: '/offer/:offerId',
    name: 'ViewOffer',
    props: { offer: null },
    component: () => import('../views/ViewOffer.vue')
  },
  {
    path: '/assignments/:assignmentId',
    name: 'ViewAssignment',
    props: { homeworkId: Number, studentId: Number },
    component: () => import('../views/ViewAssignment.vue')
  },
  {
    path: '/homework/:homeworkId',
    name: 'Assignments',
    component: () => import('../views/Assignments.vue')
  },
  {
    path: '/teachers',
    name: 'Teachers',
    component: () => import('../views/Teachers.vue')
  },
  {
    path: '/offers',
    name: 'Offers',
    component: () => import('../views/Offers.vue')
  },
  {
    path: '/courses',
    name: 'Courses',
    component: () => import('../views/Courses.vue')
  }
]

const router = new VueRouter({
  routes,
  mode: 'history',
})

router.beforeEach((to, from, next) => {
  if (to.name !== 'Login' && store.state.token == '') next({ name: 'Login' })
  else next()
})

export default router
